'use client';

import React, { useState, useRef, useCallback } from 'react';
import Image from 'next/image';
import { Plus, Minus, RotateCcw, Maximize } from 'lucide-react';
import { Button } from '@/components/ui/button';

interface ZoomableImageProps {
  src: string;
  alt: string;
  width?: number;
  height?: number;
  className?: string;
  'data-ai-hint'?: string;
}

export default function ZoomableImage({ 
  src, 
  alt, 
  width = 1200, 
  height = 700, 
  className = "",
  'data-ai-hint': dataAiHint 
}: ZoomableImageProps) {
  const [scale, setScale] = useState(1);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [lastTouchDistance, setLastTouchDistance] = useState<number | null>(null);
  const [initialTouchDistance, setInitialTouchDistance] = useState<number | null>(null);
  const [initialScale, setInitialScale] = useState<number>(1);
  const containerRef = useRef<HTMLDivElement>(null);
  const imageRef = useRef<HTMLDivElement>(null);

  const minScale = 0.5;
  const maxScale = 4;
  const scaleStep = 0.2;
  const scrollSensitivity = 0.0005; // Very low sensitivity for scroll zoom
  const pinchSensitivity = 0.005; // Reduced sensitivity for pinch zoom

  const handleZoomIn = useCallback(() => {
    setScale(prev => Math.min(prev + scaleStep, maxScale));
  }, []);

  const handleZoomOut = useCallback(() => {
    setScale(prev => Math.max(prev - scaleStep, minScale));
  }, []);

  const handleReset = useCallback(() => {
    setScale(1);
    setPosition({ x: 0, y: 0 });
  }, []);

  const handleWheel = useCallback((e: React.WheelEvent) => {
    e.preventDefault();
    
    // Detect if it's a trackpad (usually has smaller deltaY values and ctrlKey for pinch)
    const isTrackpadPinch = e.ctrlKey || Math.abs(e.deltaY) < 50;
    const sensitivity = isTrackpadPinch ? pinchSensitivity : scrollSensitivity;
    
    // Use deltaY directly for more precise control
    const delta = -e.deltaY * sensitivity;
    setScale(prev => Math.max(minScale, Math.min(maxScale, prev + delta)));
  }, [pinchSensitivity, scrollSensitivity]);

  const handleMouseDown = useCallback((e: React.MouseEvent) => {
    if (scale > 1) {
      setIsDragging(true);
      setDragStart({
        x: e.clientX - position.x,
        y: e.clientY - position.y
      });
    }
  }, [scale, position]);

  const handleMouseMove = useCallback((e: React.MouseEvent) => {
    if (isDragging && scale > 1) {
      setPosition({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y
      });
    }
  }, [isDragging, scale, dragStart]);

  const handleMouseUp = useCallback(() => {
    setIsDragging(false);
  }, []);

  // Helper function to calculate distance between two touches
  const getTouchDistance = useCallback((touches: TouchList): number => {
    if (touches.length < 2) return 0;
    const touch1 = touches[0];
    const touch2 = touches[1];
    return Math.sqrt(
      Math.pow(touch2.clientX - touch1.clientX, 2) +
      Math.pow(touch2.clientY - touch1.clientY, 2)
    );
  }, []);

  // Touch event handlers for pinch-to-zoom
  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    if (e.touches.length === 2) {
      e.preventDefault();
      const distance = getTouchDistance(e.touches);
      setInitialTouchDistance(distance);
      setLastTouchDistance(distance);
      setInitialScale(scale);
    } else if (e.touches.length === 1 && scale > 1) {
      // Single touch for panning when zoomed in
      const touch = e.touches[0];
      setIsDragging(true);
      setDragStart({
        x: touch.clientX - position.x,
        y: touch.clientY - position.y
      });
    }
  }, [scale, position, getTouchDistance]);

  const handleTouchMove = useCallback((e: React.TouchEvent) => {
    if (e.touches.length === 2 && initialTouchDistance && lastTouchDistance) {
      e.preventDefault();
      const currentDistance = getTouchDistance(e.touches);
      
      // Calculate scale change with reduced sensitivity
      const scaleChange = (currentDistance - initialTouchDistance) * pinchSensitivity;
      const newScale = Math.max(minScale, Math.min(maxScale, initialScale + scaleChange));
      
      setScale(newScale);
      setLastTouchDistance(currentDistance);
    } else if (e.touches.length === 1 && isDragging && scale > 1) {
      // Single touch panning
      const touch = e.touches[0];
      setPosition({
        x: touch.clientX - dragStart.x,
        y: touch.clientY - dragStart.y
      });
    }
  }, [initialTouchDistance, lastTouchDistance, initialScale, pinchSensitivity, isDragging, scale, dragStart, getTouchDistance]);

  const handleTouchEnd = useCallback((e: React.TouchEvent) => {
    if (e.touches.length === 0) {
      // All touches ended
      setInitialTouchDistance(null);
      setLastTouchDistance(null);
      setIsDragging(false);
    } else if (e.touches.length === 1) {
      // One touch remaining, reset pinch state but maintain pan if needed
      setInitialTouchDistance(null);
      setLastTouchDistance(null);
    }
  }, []);

  const handleFullscreen = useCallback(() => {
    setIsFullscreen(!isFullscreen);
    if (!isFullscreen) {
      setScale(1);
      setPosition({ x: 0, y: 0 });
    }
  }, [isFullscreen]);

  const handleKeyDown = useCallback((e: KeyboardEvent) => {
    if (isFullscreen) {
      switch (e.key) {
        case 'Escape':
          setIsFullscreen(false);
          break;
        case '+':
        case '=':
          handleZoomIn();
          break;
        case '-':
          handleZoomOut();
          break;
        case '0':
          handleReset();
          break;
      }
    }
  }, [isFullscreen, handleZoomIn, handleZoomOut, handleReset]);

  React.useEffect(() => {
    if (isFullscreen) {
      document.addEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'hidden';
    } else {
      document.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'unset';
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'unset';
    };
  }, [isFullscreen, handleKeyDown]);

  return (
    <>
      <div 
        ref={containerRef}
        className={`relative group overflow-hidden rounded-lg border ${className}`}
        style={{ maxHeight: '80vh' }}
      >
        <div
          ref={imageRef}
          className="relative cursor-grab active:cursor-grabbing touch-none"
          style={{
            transform: `scale(${scale}) translate(${position.x}px, ${position.y}px)`,
            transformOrigin: 'center center',
            transition: isDragging ? 'none' : 'transform 0.2s ease-out'
          }}
          onWheel={handleWheel}
          onMouseDown={handleMouseDown}
          onMouseMove={handleMouseMove}
          onMouseUp={handleMouseUp}
          onMouseLeave={handleMouseUp}
          onTouchStart={handleTouchStart}
          onTouchMove={handleTouchMove}
          onTouchEnd={handleTouchEnd}
        >
          <Image
            src={src}
            alt={alt}
            width={width}
            height={height}
            className="w-full h-auto object-contain"
            data-ai-hint={dataAiHint}
            draggable={false}
          />
        </div>

        {/* Zoom Controls */}
        <div className="absolute top-4 right-4 flex flex-col gap-2 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
          <Button
            size="sm"
            variant="secondary"
            onClick={handleZoomIn}
            disabled={scale >= maxScale}
            className="w-10 h-10 p-0 bg-background/80 backdrop-blur-sm hover:bg-background/90"
          >
            <Plus className="h-4 w-4" />
          </Button>
          <Button
            size="sm"
            variant="secondary"
            onClick={handleZoomOut}
            disabled={scale <= minScale}
            className="w-10 h-10 p-0 bg-background/80 backdrop-blur-sm hover:bg-background/90"
          >
            <Minus className="h-4 w-4" />
          </Button>
          <Button
            size="sm"
            variant="secondary"
            onClick={handleReset}
            className="w-10 h-10 p-0 bg-background/80 backdrop-blur-sm hover:bg-background/90"
          >
            <RotateCcw className="h-4 w-4" />
          </Button>
          <Button
            size="sm"
            variant="secondary"
            onClick={handleFullscreen}
            className="w-10 h-10 p-0 bg-background/80 backdrop-blur-sm hover:bg-background/90"
          >
            <Maximize className="h-4 w-4" />
          </Button>
        </div>

        {/* Zoom Level Indicator */}
        {scale !== 1 && (
          <div className="absolute bottom-4 left-4 px-2 py-1 bg-background/80 backdrop-blur-sm rounded text-sm font-mono opacity-0 group-hover:opacity-100 transition-opacity duration-200">
            {Math.round(scale * 100)}%
          </div>
        )}

        {/* Instructions */}
        <div className="absolute bottom-4 right-4 px-2 py-1 bg-background/80 backdrop-blur-sm rounded text-xs text-muted-foreground opacity-0 group-hover:opacity-100 transition-opacity duration-200">
          Scroll/Pinch to zoom • Drag to pan
        </div>
      </div>

      {/* Fullscreen Modal */}
      {isFullscreen && (
        <div className="fixed inset-0 z-50 bg-black/90 flex items-center justify-center">
          <div 
            className="relative w-full h-full flex items-center justify-center cursor-grab active:cursor-grabbing touch-none"
            style={{
              transform: `scale(${scale}) translate(${position.x}px, ${position.y}px)`,
              transformOrigin: 'center center',
              transition: isDragging ? 'none' : 'transform 0.2s ease-out'
            }}
            onWheel={handleWheel}
            onMouseDown={handleMouseDown}
            onMouseMove={handleMouseMove}
            onMouseUp={handleMouseUp}
            onMouseLeave={handleMouseUp}
            onTouchStart={handleTouchStart}
            onTouchMove={handleTouchMove}
            onTouchEnd={handleTouchEnd}
          >
            <Image
              src={src}
              alt={alt}
              width={width}
              height={height}
              className="max-w-[90vw] max-h-[90vh] object-contain"
              data-ai-hint={dataAiHint}
              draggable={false}
            />
          </div>

          {/* Fullscreen Controls */}
          <div className="absolute top-6 right-6 flex gap-2">
            <Button
              size="sm"
              variant="secondary"
              onClick={handleZoomIn}
              disabled={scale >= maxScale}
              className="bg-background/20 backdrop-blur-sm hover:bg-background/30"
            >
              <Plus className="h-4 w-4" />
            </Button>
            <Button
              size="sm"
              variant="secondary"
              onClick={handleZoomOut}
              disabled={scale <= minScale}
              className="bg-background/20 backdrop-blur-sm hover:bg-background/30"
            >
              <Minus className="h-4 w-4" />
            </Button>
            <Button
              size="sm"
              variant="secondary"
              onClick={handleReset}
              className="bg-background/20 backdrop-blur-sm hover:bg-background/30"
            >
              <RotateCcw className="h-4 w-4" />
            </Button>
            <Button
              size="sm"
              variant="secondary"
              onClick={handleFullscreen}
              className="bg-background/20 backdrop-blur-sm hover:bg-background/30"
            >
              ×
            </Button>
          </div>

          {/* Fullscreen Instructions */}
          <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 px-4 py-2 bg-background/20 backdrop-blur-sm rounded text-white text-center">
            <div className="text-sm">Scroll/Pinch to zoom • Drag to pan</div>
            <div className="text-xs text-white/70 mt-1">Press ESC or click × to exit • +/- keys to zoom • 0 to reset</div>
          </div>

          {/* Zoom Level in Fullscreen */}
          {scale !== 1 && (
            <div className="absolute top-6 left-6 px-3 py-2 bg-background/20 backdrop-blur-sm rounded text-white font-mono">
              {Math.round(scale * 100)}%
            </div>
          )}
        </div>
      )}
    </>
  );
}